import React, { Component } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import GitHubIcon from '@material-ui/icons/GitHub';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import MainFeaturedPost from './MainFeaturedPost';
import { Card } from 'react-bootstrap';
import './MainPage.css';

const useStyles = makeStyles((theme) => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
}));


const mainFeaturedPost = {
  title: '¡Bienvenid@ a School2Cool!',
  description: "Una nueva forma de lograr tus metas eficazmente.",
  image: 'https://images.unsplash.com/photo-1485322551133-3a4c27a9d925?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=500&q=60',
  imgText: 'imagen descriptiva',
};

const features = [
  {
    id: 0,
    name: 'Crea tus aulas',
    image: 'https://www.neothek.com/libs17/img/diseno/aulavirtual/aula-virtual.png',
    descrip: 'Como docente puedes crear aulas de acuerdo a tus estándares.'
  },
  {
    id: 1,
    name: 'Asignaciones',
    image: 'https://cdn0.iconfinder.com/data/icons/summer-193/512/School-College-outline_paper_work-deadline-homework-report-essay-512.png',
    descrip: 'Al crear asignaciones tus estudiantes conocerán lo que esperas de ellos.'
  },
  {
    id: 2,
    name: 'Rúbricas personalizadas',
    image: 'https://cdn.pixabay.com/photo/2017/06/10/07/18/list-2389219_960_720.png',
    descrip: 'Las rúbricas de evalución son muy importantes para los estudiantes.'
  },
  // {
  //   id: 3,
  //   name: 'Crea aulas',
  //   image: '',
  //   descrip: 'Como docente puedes crear aulas de acuerdo a tus estándares'
  // },
]


export default function MainPage() {
  return (
    <Container>
      <MainFeaturedPost post={mainFeaturedPost} />

      <div id="Content">
        {
          features.map((ftr) => {
            return (
              <Card style={{ width: '18rem' }} id="singleCard">
                <Card.Img variant="top" src={ ftr.image } />
                <Card.Body>
                  <Card.Title key = { ftr.id }>{ ftr.name }</Card.Title>
                  <Card.Text>
                    { ftr.descrip }
                  </Card.Text>
                </Card.Body>
              </Card>
            )
          })
        }
        <div id="SocialMedias">
          <GitHubIcon />
          <FacebookIcon />
          <TwitterIcon />
        </div>
      </div>
    </Container>
  );
}